.ca-container {

	& .ca-card {
		position: relative;
		margin-top: 100px;
			-webkit-box-shadow: 0 2px 5px 0 rgba(0,0,0,.16), 0 2px 10px 0 rgba(0,0,0,.12);
			box-shadow: 0 2px 5px 0 rgba(0,0,0,.16), 0 2px 10px 0 rgba(0,0,0,.12);

			& .ca-card-head {
				padding: 10px 20px;
			}

			& .ca-card-head-center {
				width: 92%;
				text-align: center;
				position: absolute;
				padding: 10px 20px;

				margin-left: 4%;
				margin-right: 4%;
				margin-top: -20px;
			-webkit-box-shadow: 0 2px 5px 0 rgba(0,0,0,.16), 0 2px 10px 0 rgba(0,0,0,.12);
			box-shadow: 0 2px 5px 0 rgba(0,0,0,.16), 0 2px 10px 0 rgba(0,0,0,.12);

			}




			& .ca-card-body {
				padding: 10px 20px;
			}
	}





}